<template>
  <n-config-provider :theme="$store.getters.theme" :locale="$store.getters.locale"
    :date-locale="$store.getters.locale_d">
    <n-dialog-provider>
      <n-message-provider>
        <n-loading-bar-provider>
          <router-view></router-view>
        </n-loading-bar-provider>
      </n-message-provider>
    </n-dialog-provider>
  </n-config-provider>
</template>

<script>
import axios from "axios";
export default {
  data() {
    // 预览环境数据存放位置
    axios.interceptors.request.use((req) => {
      // 打上标记
      if (/^\/items\/.*/.test(req.url)) req.item = req.url;
      if (/^\/items\/\?author=(.*)/.test(req.url)) req.self = req.url;
      if (/^\/items\/.*/.test(req.url)) req.url = "/items";

      if (/^\/comment\/.*/.test(req.url)) req.url = "/comment";

      if (/^\/users\/$/.test(req.url)) req.url = "/users/1664544781096859";
      if (/^\/login\/.*/.test(req.url)) req.url = "/users/1664544781096859";

      if (/^\/info\/.*/.test(req.url)) req.info = req.url;
      if (/^\/info\/.*/.test(req.url)) req.url = "/info";
      req.method = "GET"
      return req;
    });
    // 预览环境数据存提取方式
    axios.interceptors.response.use(
      (req) => {
        // 只返回id部分
        if (req.config.item) {
          for (let take of req.data) {
            if (this.$route.params.id == take.id) {
              req.data = take;
            }
          }
        };
        // 只返回自己部分
        if (req.config.self) {
          let id = /^\/items\/\?author=(.*)/.exec(req.config.self);
          // 如果存在作者筛选
          if (id[1]) {
            let data = [];
            for (let take of req.data) {
              if (id[1] == take.author) {
                data.push(take);
              }
            }
            req.data = data;
          }
        };
        // 只返回id信息
        // if (req.config.info) {
        //   if (req.config.info == "/info/") {
        //     return req
        //   }
        //   req.data = req.data[req.config.info.substring(6, req.config.info.length)];
        // };
        if (req.config.info) {
          console.log(req.config.info);
          let id = /info\/(.*)/.exec(req.config.info)
          if (id[1]) {
            req.data = req.data[id[1]]
          }
        }
        return req
      },
      (err) => {
        console.log("error", err);
      }
    );
    return {};
  },
};
</script>